.tutor-popup-opener {
  position: relative;
  display: inline-block;
  padding: 0;
}

.tutor-popup-opener .popup-btn {
  display: inline-block;
  outline: none;
  border: none;
  cursor: pointer;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 3px;
}

.tutor-popup-opener .popup-btn > * {
  pointer-events: none;
  font-size: 20px;
}

.tutor-popup-opener .popup-btn:hover, .tutor-popup-opener .popup-btn:focus {
  box-shadow: 0 0 0 2px var(--tutor-primary-lighter);
}

.tutor-popup-opener .popup-btn .toggle-icon {
  width: 20px;
  height: 20px;
  background-image: url("data:image/svg+xml;charset=UTF-8, <svg xmlns='http://www.w3.org/2000/svg' width='48' height='48' viewBox='0 0 48 48' fill='none'><path fill-rule='evenodd' clip-rule='evenodd' d='M24 12C26.2091 12 28 10.2091 28 8C28 5.79086 26.2091 4 24 4C21.7909 4 20 5.79086 20 8C20 10.2091 21.7909 12 24 12ZM24 28C26.2091 28 28 26.2091 28 24C28 21.7909 26.2091 20 24 20C21.7909 20 20 21.7909 20 24C20 26.2091 21.7909 28 24 28ZM28 40C28 42.2091 26.2091 44 24 44C21.7909 44 20 42.2091 20 40C20 37.7909 21.7909 36 24 36C26.2091 36 28 37.7909 28 40Z' fill='%231a1b1e'/></svg>");
  background-repeat: no-repeat;
  background-size: 20px;
  background-position: center;
}

.tutor-popup-opener .popup-menu {
  background: #212327;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  top: calc(100% + 1.5px);
  min-width: 170px;
  right: -6px;
  transition: 200ms ease-out;
  opacity: 0;
  visibility: hidden;
}

.tutor-popup-opener .popup-menu:before {
  content: '';
  position: absolute;
  border: 6px solid transparent;
  border-bottom-color: #212327;
  top: -12px;
  right: 10px;
}

.tutor-popup-opener .popup-menu li {
  list-style: none;
}

.tutor-popup-opener .popup-menu li:not(:last-child) a {
  margin-bottom: 2px;
}

.tutor-popup-opener .popup-menu li a {
  display: flex;
  align-items: center;
  color: #ffffff;
  text-decoration: none;
  padding: 5px 12px;
  transition: 200ms;
}

.tutor-popup-opener .popup-menu li a:hover {
  background: #41454f;
}

.tutor-popup-opener .popup-menu li a i,
.tutor-popup-opener .popup-menu li a .icon {
  font-size: 26px;
  margin-right: 8px;
}

.tutor-popup-opener .popup-menu.visible {
  opacity: 1;
  visibility: visible;
  animation-name: fadeIn;
  animation-duration: 300ms;
  animation-timing-function: cubic-bezier(0.74, 0.49, 0.24, 0.96);
}

@keyframes fadeIn {
  from {
    transform: translateY(2px);
    opacity: 0;
  }
  to {
    transform: translateY(0px);
    filter: blur(0);
    opacity: 1;
  }
}
